import { DEL_CITY } from "./actions";
import { defineState } from "redux-localstore";
import { getState } from "redux-localstore";

const defaultState = getState() || {
  cities: null
};

const initialState = defineState(defaultState)("city_reducer");

export function city_reducer(state = initialState, action) {
  switch (action.type) {
    case DEL_CITY:
      let cityNameToDelete = action.payload;
      let cityArrCopy = JSON.parse(JSON.stringify(state.cities));
      let newCitiesArr = cityArrCopy.filter(city => city.name !== cityNameToDelete);
      return {
        ...state,
        cities: newCitiesArr
      };
    default:
      return state;
  }
}



